@import '../common.scss';

.casting_form-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    font-size: 24px;
    padding-top: 50px;
    @media (max-width: 800px) {
      flex-direction: column;
      overflow: hidden;
  }
}

.casting_terms {
  display: flex;
  align-items: center;
}

.casting_terms-modal-button {
  color: $blue;
  font-weight: 400;
  margin-left: 30px;
  text-decoration: underline;
  cursor: pointer;
}

.blur-panel {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: flex;
  justify-content: center;
  color: $blue;
  align-items: center;
  backdrop-filter: blur(2px);
  div {
    font-size: 40px;
    font-weight: 400;
    text-shadow: 2px 7px 5px rgba(0,0,0,0.3), 
    0px -4px 10px rgba(255,255,255,0.3);
  }
}

.casting_form-wrapper {
  position: relative;
}