@import '../../style.scss';

.casting_more-info {
  margin-bottom: 30px;
  font-weight: 400;
  color: $blue;
  cursor: pointer;
  font-size: 18px;
}

.casting_moreInfo {
  width: 90%;
  display: none;
  font-size: 28px;

  .casting_moreInfo_item {
    margin-bottom: 50px;
    &:last-child {
      margin-bottom: 20px;
    }
    .casting_moreInfo_title {
      font-weight: 400;
      color: $blue;
    }
  }
}
.casting_moreInfo-visible {
  display: block;
}
.casting_steps-body {
  width: 90%;
  font-size: 26px;
}

.befree-text-casting {
  font-size: 24px;
  width: 90%;
  color: $textColor;
}

.dashboard__table {
  display: flex;
  justify-content: space-around;
  .table-item {
    min-width: 400px;
    max-width: 400px;
    min-height: 250px;
    border-radius: 100px;
    border: 2px solid $grey;
    margin-right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 30px;
    
    &:last-child {
      margin-right: 0px;
    }
    .table-title {
      margin-bottom: 20px;
      color: $blue;
      font-weight: 400;
    }
    p {
      text-align: center;
      font-weight: 400;
    }
    @media (max-width: 400px) {
      min-width: 0;
    }
  }

  @media (max-width: 850px) {
    flex-direction: column;
    .table-item {
      margin-right: 0px;
      max-width: 100%;
      margin-bottom: 30px;
      border-radius: 50px;
    }
  }
}

@media (max-width: 1600px) {
  .casting_steps-body {
    width: 90%;
    font-size: 20px;
  }
  .casting_moreInfo {
    font-size: 20px;
  }
  .befree-text-casting {
    font-size: 20px;
  }
}

@media (max-width: 1440px) {
  .casting_steps-body {
    width: 90%;
    font-size: 16px;
  }
  .casting_moreInfo {
    font-size: 16px;
  }
  .befree-text-casting {
    font-size: 18px;
  }
}
